<template>
    <el-container>
        <el-header>
            <el-input type="text" v-model="serchkey" placeholder="请输入查询关键字"
                      style="min-width: 200px;max-width: 400px;" clearable prefix-icon="el-icon-search"></el-input>
            <el-button type="primary" @click="reload()" icon="el-icon-search">查询</el-button>
            <el-button type="info" @click="add()" icon="el-icon-news">新增</el-button>
        </el-header>
        <el-main>
            <el-row>
                <el-col>
                    <el-table
                            :data="tableData"
                            border
                            stripe
                            style="width: 100%;">
                        <el-table-column
                                prop="name"
                                label="用户组名称">
                        </el-table-column>


                        <el-table-column
                                prop="createDate"
                                label="创建时间">
                        </el-table-column>

                        <el-table-column
                                fixed="right"
                                label="操作"
                                width="185">
                            <template slot-scope="scope">
                                <el-button @click="editHandleClick(scope.row.id,scope.row.name)" size="mini" type="primary"  icon="el-icon-edit">
                                    编辑
                                </el-button>
                                <el-button @click="deleteHandleClick(scope.row.id)" type="danger" size="mini" icon="el-icon-delete">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row>
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="countSize">
                </el-pagination>
            </el-row>
        </el-main>
    </el-container>
</template>
<style>
    .el-input {
        margin-right: 10px;
    }

    .el-input__inner {
        width: 90%;
    }

    .el-table td {
        padding: 5px 0;
    }
</style>
<script>
    export default {
        data() {
            return {
                serchkey: '',
                tableData: null,
                currentPage: 1,
                countSize: 0,
                pageSize: 1
            }
        },
        mounted() {
            this.reload();
        },
        methods: {
            add() {
                this.$router.push({path: '/userGroupManage/add', replace: true});
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.reload();
            },
            reload() {
                // var lang = localStorage.getItem("language");
                this.axios.get('/api/userGroup/page?serchkey=' + this.serchkey + "&page=" + this.currentPage).then((response) => {
                    this.tableData = response.data.data.content;
                    this.currentPage = response.data.data.number + 1;
                    this.countSize = response.data.data.totalElements;
                    this.pageSize = response.data.data.size;
                }).catch((response) => {
                    console.log(response);
                })
            },
            editHandleClick(id, name) {
                this.$router.push({path: '/userGroupManage/add', query: {id: id, name: name}, replace: true});
            },
            deleteHandleClick(id) {
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.post('/api/userGroup/delete/' + id).then((response) => {
                        if (response.data.state == '0') {
                            //刷新数据
                            this.reload();
                        } else {
                            console.log(response);
                            this.$message({
                                type: 'error',
                                message: '删除失败'
                            });
                        }
                    }).catch((response) => {
                        console.log(response);
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
    }
</script>
